










































































































































import Vue from 'vue';

export default Vue.extend({
  name: 'mainPage',
  computed: {
    picture1() {
      return this.$store.getters.getTodoById(1);
    },
    picture2() {
      return this.$store.getters.getTodoById(2);
    },
  },
  methods: {
    clicked(id: number) {
      this.$store.dispatch('ADD_PICTURES', id);
    },
  },
});
